@import "variable";

.bt1 {
  border-top: 1rpx solid #F1F1F1;
}

.bd1 {
  border-bottom: 1rpx solid #F1F1F1;
}

.bd20 {
  border-bottom: 20rpx solid @tabBgColor;
}

.through-line {
  text-decoration: line-through;
}

@keyframes biling {
  from {
    left: -100%;
  }
  to {
    left: 120%;
  }
}
.bg-img-bling {
  position: relative;
  overflow: hidden;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    z-index: 100;
    left: -40%;
    width: 50%;
    height: 100%;
    opacity: 0.5;
    background-image: linear-gradient(90deg,rgba(255,255,255,0.1),rgba(255,255,255,0.5),rgba(255,255,255,0.1));
    transform: skewX(-25deg);
    animation: 1.5s biling linear infinite;
  }
}